/* ÌÜ†Í∏Ä */
.toggle {
  position: relative;
  display: inline-block;
  width: 65px;
  height: 34px;
}

.toggle input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #CD853F;
  transition: .3s;
  border-radius: 34px;
  border: 4px solid #8B4513;
}

.slider:before {
  position: absolute;
  content: "üêª";
  height: 22px;
  width: 22px;
  left: 3px;
  bottom: 2px;
  background-color: white;
  transition: .3s;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
}

input:checked + .slider {
  background: linear-gradient(135deg, #FFC658 0%, #FFD700 100%);
}

input:checked + .slider:before {
  transform: translateX(28px);
  content: "üçØ";
}

